.icon {
  display: block;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.BLUE {
  background: #3165b1;
}
.FIRE {
  background: #fbb017;
}
.YELLOW {
  background: #f7ef66;
}
.PINK {
  background: #f07aac;
}
.GREY {
  background: #5c6f7d;
}
.SKY {
  background: #14bbef;
}
.BROWN {
  background: #7d3d22;
}
.HUNTER {
  background: #00794c;
}

.active {
  color: #1677ff;
  font-size: 14px;
  background: #f0f0f0;
}
.activeDice {
  color: #1677ff;
  font-size: 16px;
}

.color {
  display: flex;
}

.box {
  padding: 4px;
  border-radius: 8px;
  border: 2px solid transparent;
  text-align: center;
}

.dice {
  display: flex;
}
