.list {
  height: 100%;
  overflow-y: auto;
  flex-basis: 35%;
  background: #3a3535;
  padding-top: 2px;
  box-sizing: border-box;
}

.list::-webkit-scrollbar {
  display: none;
}

.item {
  background: #201c1c;
  padding: 0.5vh;
  box-sizing: border-box;
  border-top: 2px solid #3a3535;
  display: flex;
  position: relative;
}
.item .selectedOutline {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  box-sizing: border-box;
  border: 0.5vh solid #f50;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5) inset;
}

.portait {
}

.portait img {
  object-fit: cover;
  width: 15vh;
  height: 20vh;
}

.tag {
  border-radius: 20px;
  white-space: nowrap;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  border: 2px solid;
  font-size: 1.2vh;
  padding: 2px 6px;
}
.physic {
  border-color: #1abc9c;
  color: #1abc9c;
}
.magic {
  border-color: #f50;
  color: #f50;
}
.ranged {
  border-color: #74b9ff;
  color: #74b9ff;
}

.resist {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.strong {
  font-weight: bold;
  margin-left: 6px;
  color: #fff;
}

.row {
  display: flex;
  align-items: center;
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.6vh;
  padding: 4px 0;
}
.row i {
  margin-right: 5px;
}

.content {
  padding: 1.5vh;
}

.resist {
  padding: 5px 0;
  margin-top: 8px;
}

.status {
  display: flex;
  padding-top: 15px;
  width: 100%;
}
.status .block {
  flex: 1;
}
